.modal {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  border-radius: 10px;
  width: 80%;
  max-width: 800px;
  height: 80%;
  margin: 5% auto;
  padding: 20px;
  overflow: scroll;
}

.btn-close {
  color: #aaa;
  float: right;
  font-weight: bold;
}

.btn-close:hover, .btn-close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.modal-header {
  border-bottom: 1px solid #dee2e6;
  padding: 2px 16px;
  font-size: 150%;
}

.modal-body {
  padding: 2px 16px;
}

.modal-footer {
  color: #fff;
  background-color: #5cb85c;
  padding: 2px 16px;
}

